@charset "UTF-8";
body {
  background-image: url(http://bgrounds.ru/_include/img/bgs/scratone/white-scratone.png);
  margin: 0;
  font-family: 'Comfortaa'; }

.component {
  height: calc(100vh);
  /* фолбэк для браузеров, которые не поддерживают пользовательские свойства */
  height: calc(var(--vh, 1vh) * 100); }

.template {
  height: calc(100% - 90px);
  max-width: 1600px;
  margin: 0 auto;
  position: relative;
  overflow: hidden; }

.actions {
  height: 90px;
  border-top: 1px dashed silver;
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 0 15px; }
  .actions__input-count, .actions__input-size {
    width: 100%;
    max-width: 500px;
    user-select: none;
    position: relative;
    top: 3px; }
  .actions__input-count {
    margin-right: 20px; }
  .actions__input-size {
    margin-left: 20px; }
  .actions__text-count, .actions__text-size {
    text-align: center;
    font-size: 13px;
    position: relative;
    top: 3px; }

.actions .input-range__label--min,
.actions .input-range__label--max {
  bottom: -1.3rem; }

.dot {
  height: 20px;
  width: 20px;
  background: #000;
  border-radius: 50%;
  will-change: transform;
  transition: .7s transform ease, .6s background-color ease, .7s width ease, .7s height ease;
  position: absolute; }

.device-info {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: #eff8fa;
  text-align: center;
  display: none;
  align-items: center;
  padding: 0 5px; }

.btn {
  cursor: pointer;
  min-width: 140px; }

@media screen and (max-width: 900px) {
  .template {
    height: calc(100% - 70px); }
  .actions {
    height: 70px; } }

@media screen and (max-width: 750px) {
  .template {
    height: calc(100% - 65px); }
  .actions {
    height: 65px; } }

@media screen and (max-width: 600px) {
  .template {
    height: calc(100% - 60px); }
  .actions {
    height: 60px; } }

@media screen and (max-width: 600px) {
  .btn {
    font-size: 14px; } }

@media screen and (orientation: portrait) {
  .device-info {
    display: flex;
    justify-content: center; } }

